import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { Link } from 'react-router-dom';
import Editor from 'for-editor';
import {
  WriteWrapper,
  WriteBackHome,
  PublishArticles
} from './style';

class Home extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
      toolbar: {
        h1: true, // h1
        h2: true, // h2
        h3: true, // h3
        h4: true, // h4
        img: true, // 
        link: true, // 
        code: true, // 
        preview: true, // 
        expand: false, // 
        undo: true, // 
        redo: true, // 
        save: true, // 
        subfield: true, // 
      },
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleOnSave = this.handleOnSave.bind(this);
  }
  
  render() {
    if (!this.props.whetherSignIn) {
      return <Redirect to="/sign_in"/>
    }
    const { value, toolbar } = this.state;
    return (
      <WriteWrapper>
        <WriteBackHome>
          <input placeholder="..."/>
          <PublishArticles>
            <Link to="/">
              <span className="back-home"></span>
            </Link>
            <span></span>
            <img alt="" src="https://user-gold-cdn.xitu.io/2019/3/18/1699037a6f8c50ee?imageView2/1/w/64/h/64/q/85/interlace/1"/>
          </PublishArticles>
        </WriteBackHome>
        <Editor
          value={value}
          placeholder="..."
          toolbar={toolbar}
          lineNum={false}
          preview={true}
          subfield={true}
          onChange={this.handleChange}
          onSave={this.handleOnSave}
        />
      </WriteWrapper>
    )
  }

  handleChange(value) {
    this.setState({
      value
    })
  }

  handleOnSave(e) {
    console.log(this.state.value);
  }

}
const mapState = (state) => {
  return {
    whetherSignIn: state.getIn(['signIn', 'whetherSignIn'])
  }
};
const mapDispath = (despatch) => {
  return {
  }
}

export default connect(mapState, mapDispath)(Home);